#listContainer {
    width: 96%;
    height: 580px;
    margin-left: 2%;
    margin-top: 5px;
    font-family: "Arial";
    overflow-x: hidden;
    overflow-y: scroll;
}

::-webkit-scrollbar {
    width: 5px;
    background: transparent;
}

::-webkit-scrollbar-thumb {
    background: gray;
}